
// Color palette export

$colorList: (
    "blues":       $blues,
    "indigos":     $indigos,
    "purples":     $purples,
    "pinks":       $pinks,
    "reds":        $reds,
    "oranges":     $oranges,
    "yellows":     $yellows,
    "greens":      $greens,
    "teals":       $teals,
    "cyans":       $cyans
);

// Light Theme Color Map - original

$light-theme-colors: (
     "primary":    $indigo,
     "secondary":  $gray-700,
     "success":    $green,
     "info":       $cyan,
     "warning":    $yellow,
     "danger":     $red,
     "light":      $gray-200,
     "dark":       $gray-900
);

// Dark Theme Color Map - original

$dark-theme-colors-hyper: (
     "primary":    map-get($light-theme-colors, "primary"),
     "secondary":  map-get($dark-grays, "600"),
     "success":    map-get($light-theme-colors, "success"),
     "info":       map-get($light-theme-colors, "info"),
     "warning":    map-get($light-theme-colors, "warning"),
     "danger":     map-get($light-theme-colors, "danger"),
     "light":      map-get($dark-grays, "300"),
     "dark":       map-get($dark-grays, "900")
);

$sabee-colors: (
    "010":      $sabee-blue,
    "020":      $sabee-blue-darker,
    "030":      $sabee-red,
    "040":      $sabee-red-darker,
    "050":      $sabee-yellow,
    "060":      $sabee-yellow-darker,
    "070":      $sabee-green,
    "080":      $sabee-green-darker,
    "090":      $sabee-cyan,
    "100":      $sabee-cyan-darker
);

$dark-theme-colors: (
        "primary":    $primary,
        "secondary":  map-get($sabee-dark-grays, "600"),
        "success":    $success,
        "info":       $info,
        "warning":    $warning,
        "danger":     $danger,
        "light":      map-get($sabee-dark-grays, "300"),
        "dark":       map-get($sabee-dark-grays, "900")
);

