.card[data-color] ul > li {
    text-align: center !important;
    text-transform: uppercase;
    font-weight: bold;
    white-space: nowrap;
}

:root{

    /* Color palettes - Bootstrap */
    @each $colorsName, $colors in $colorList-bootstrap {

        @each $colorName, $colorValue in $colors {
            --color-bootstrap-#{$colorName}: #{$colorValue};
        }
    }
    /* Grays - Bootstrap */
    @each $colorName, $colorValue in $grays-bootstrap {
        --color-gray-bootstrap-#{$colorName}: #{$colorValue};
    }
    /* Color palettes - Hyper */
    @each $colorsName, $colors in $colorList {

        @each $colorName, $colorValue in $colors {
            --color-#{$colorName}: #{$colorValue};
        }
    }
    /* Grays - Hyper */
    @each $colorName, $colorValue in $grays {
        --color-gray-#{$colorName}: #{$colorValue};	
    }
    /* Custom grays */
    @each $colorName, $colorValue in $custom-grays {
        --color-custom-gray-#{$colorName}: #{$colorValue};	
    }
    /* Custom colors */
    @each $colorName, $colorValue in $custom-colors {
        --color-custom-color-#{$colorName}: #{$colorValue};	
    }
    /* Light theme colors - Bootstrap */
    @each $colorName, $colorValue in $theme-colors-bootstrap {
        --color-theme-bootstrap-#{$colorName}: #{$colorValue};
    }
    /* Light theme colors - original */
    @each $colorName, $colorValue in $light-theme-colors {
        --color-theme-#{$colorName}: #{$colorValue};	
    }
    /* Dark theme grays */
    @each $colorName, $colorValue in $dark-grays {
        --color-gray-dark-#{$colorName}: #{$colorValue};	
    }
    /* Dark theme colors */
    @each $colorName, $colorValue in $dark-theme-colors-hyper {
        --color-theme-dark-#{$colorName}: #{$colorValue};	
    }

    /* Color palettes - Sabee */
    @each $colorsName, $colors in $sabeeColorList {

        @each $colorName, $colorValue in $colors {
            --color-sabee-#{$colorName}: #{$colorValue};
        }
    }

    /* Sabee theme colors */
    @each $colorName, $colorValue in $theme-colors {
        --color-sabee-theme-#{$colorName}: #{$colorValue};	
    }
    /* Sabee dark theme colors */
    @each $colorName, $colorValue in $dark-theme-colors {
        --color-sabee-theme-dark-#{$colorName}: #{$colorValue};
    }
    /* Sabee grays */
    @each $colorName, $colorValue in $sabee-grays {
        --color-sabee-gray-#{$colorName}: #{$colorValue};	
    }
    /* Sabee dark grays */
    @each $colorName, $colorValue in $sabee-dark-grays {
        --color-sabee-gray-dark-#{$colorName}: #{$colorValue};
    }
    /* Sabee colors */
    @each $colorName, $colorValue in $sabee-colors {
        --color-sabee-color-#{$colorName}: #{$colorValue};	
    }
}